<template>
  <li>
    <div class="wrap">
      <div class="left">
        <h3 :class="{ changered: audioStatus.songId == newsong.id }">{{ newsong.name }}</h3>
        <div class="downRow">
          <div class="sq bgrefl"></div>
          <span class="singer" :class="{ changered: audioStatus.songId == newsong.id }"
            >{{ newsong.song.artists[0].name
            }}{{
              newsong?.song?.artists[1]?.name
                ? "/" + newsong.song.artists[1].name
                : ""
            }}
            - {{ newsong.name }}</span
          >
        </div>
      </div>
      <div class="icon">
        <div
          class="play iconfont"
          @click.stop="playsong"
          v-if="!audioStatus.playing || newsong.id !== audioStatus.songId"
        >
          &#xe624;
        </div>
        <div
          class="pause iconfont"
          @click.stop="$emit('zan-ting')"
          v-if="audioStatus.playing && newsong.id == audioStatus.songId"
        >
          &#xe629;
        </div>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    audioStatus: Object,
    newsong: Object,
  },
  methods: {
    playsong: function () {
      this.$emit("play-song", this.newsong);
      // this.$emit("add-crtPlay", this.newsong);
      // console.log(1232222);
    },
  },
};
</script>

