  padding-left: 10rem;
.changered{
  color: red !important;
}
  .wrap {
    max-width: 100%;
    padding: 6rem;
    padding-left: 0;
    height: 55.5rem;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 0.1rem solid rgb(226, 226, 227);
    h3 {
      max-width: 300rem;
      font-size: 17rem;
      padding: 2rem 0;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .downRow {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .sq {
        width: 12rem;
        height: 8rem;
        // background-image: url("../assets/sq.png");
