      background-size: 166rem 97rem;
      @font-face {
        font-family: "iconfont";
        src: url("@/assets/player/iconfont.ttf") format("truetype");
      }
      .iconfont {
        font-family: "iconfont" !important;
        font-size: 22rem;
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
      }
      .play,
      .pause {
        // background-image: url('@/assets/player');
        position: absolute;
