<template>
  <div class="home">
    <div class="loading" v-if="loadingShow" ref="loading">
      <img src="../img/loading.gif" />
    </div>

    <div class="k-homeremd">
      <p class="k-edit">编辑推荐</p>
      <ul class="k-rcmd">
        <EditCard
          v-for="item in personalized"
          :key="item.id"
          :item="item"
        ></EditCard>
      </ul>
      <p class="k-edit">最新音乐</p>
      <ul class="k-newMusic">
        <NewMusic
          @play-song="playsong($event)"
          v-for="newsong in newsongs"
          :audioStatus="audioStatus"
          @zan-ting="$emit('zan-ting')"
          :key="newsong.id"
          :newsong="newsong"
          @click.native="$emit('play-song', newsong),$emit('add-crtPlay', newsong)"
        ></NewMusic>
      </ul>
      <div class="k-footer">
        <div class="firstRow">
