<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import EditCard from "@/components/EditCard.vue";
import NewMusic from "@/components/NewMusic.vue";

export default {
  components: {
    // HelloWorld
    EditCard,
    NewMusic,
  },
  props: {
    audioStatus: Object,
  },
  data() {
    return {
      personalized: [],
      newsongs: [],
      loadingShow: true,
    };
  },
  created() {
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/personalized/", {
        params: { limit: 6 },
      }),
      this.axios.get("https://apis.netstart.cn/music/personalized/newsong", {
        params: { limit: 10 },
      }),
    ]).then(([p, n]) => {
      this.loadingShow = false;
      this.personalized = p.data.result;
      this.newsongs = n.data.result;
    });
  },
  methods: {
    playsong(n) {
      this.$emit("play-song", n);
      this.$emit("add-crtPlay", n);
    },
  },
};
</script>
