<template>
  <li class="ilike">
    <div class="left">
      <div class="loading" v-show="like.id == audioStatus.songId">
        <RedLoading :audioStatus="audioStatus"></RedLoading>
      </div>
      <div v-show="like.id !== audioStatus.songId" class="num">
        {{ String(index + 1).padStart(2, "0") }}
      </div>
      <div class="songinfo">
        <p
          class="songName"
          :class="{ changered: audioStatus.songId == like.id }"
        >
          {{ songName }}
        </p>
        <div class="sq"></div>
        <p class="singer" :class="{ changered: audioStatus.songId == like.id }">
          {{ singerName }}
        </p>
      </div>
    </div>
    <div class="right" @click.stop="$emit('delete-one', like)">
      <svg
        t="1664635188758"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="2541"
        width="15"
        height="15"
      >
        <path
          fill="#a7a7a7"
          d="M547.2 512l416-416c9.6-9.6 9.6-25.6 0-35.2s-25.6-9.6-35.2 0l-416 416-416-416c-9.6-9.6-25.6-9.6-35.2 0s-9.6 25.6 0 35.2l416 416-416 416c-9.6 9.6-9.6 25.6 0 35.2s25.6 9.6 35.2 0l416-416 416 416c9.6 9.6 25.6 9.6 35.2 0s9.6-25.6 0-35.2L547.2 512z"
          p-id="2542"
        ></path>
      </svg>
    </div>
  </li>
</template>

<script>
import RedLoading from "@/components/RedLoading.vue";

export default {
  components: {
    RedLoading,
  },
  props: {
    audioStatus: Object,
    // item: Object,
    index: Number,
    like: Object,
  },
  data() {
    return {};
  },
  computed: {
    singerName() {
      return this.like?.song?.artists
        ? this.like?.song?.artists[0].name
        : this.like.ar[0]?.name;
    },
    songName() {
      return this.like?.name;
    },
  },
};
</script>

<style lang="less" scoped>
.ilike {
  display: flex;
  justify-content: space-between;
  padding: 3rem 10rem;
  align-items: center;
  .changered {
    color: red !important;
  }
  .left {
    display: flex;
    justify-content: space-between;
    // padding: 5rem 15rem;
    padding: 5rem;
    align-items: center;
    .loading {
      width: 22rem;
      height: 25rem;
      padding: 0px 10.5rem;
      box-sizing: content-box;
    }
    .num {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 43rem;
      height: 43rem;
      font-size: 20rem;
      // background-color: bisque;
      color: rgb(135, 135, 138);
    }
    .songinfo {
      .songName {
        font-size: 16rem;
      }
      .singer {
        color: rgb(141, 144, 148);
        padding: 2rem;
      }
    }
  }
  .right {
    width: 50rem;
    height: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
