<template>
  
    <div class="BigPlayer animate__animated animate__slideInUp">
      <div
        class="bgr"
        :style="`background-image: url('` + picUrl + `');`"
      ></div>
      <svg
        @click="$router.back(), $emit('show-Small')"
        t="1664105564797"
        class="packup"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="15668"
        width="30"
        height="30"
      >
        <path
          d="M490.666667 601.6L797.866667 298.666667l59.733333 59.733333-302.933333 302.933333-59.733334 64-59.733333-59.733333L128 358.4 187.733333 298.666667l302.933334 302.933333z"
          p-id="15669"
        ></path>
      </svg>
      <i
        class="fa fa-heart"
        @click="$emit('i-like', song)"
        :class="{
          like: likes.some((o) => o.id == song.id),
        }"
      ></i>
      <div class="songIfm" v-if="song?.ar">
        <h3>{{ song.name }}</h3>
        <span
          >{{ song.ar[0].name
          }}{{ song.ar[1]?.name ? "/" + song.ar[1]?.name : "" }}</span
        >
      </div>
      <div class="songIfm" v-if="song?.song?.artists[0]">
        <h3>{{ song.song.name }}</h3>
        <span
          >{{ song?.song?.artists[0]?.name
          }}{{
            song?.song?.artists[1]?.name
              ? "/" + song?.song?.artists[1]?.name
              : ""
          }}</span
        >
      </div>
      <!-- <div class="songIfm">
      <h3>{{ song }}</h3>
      <span>薛之谦</span>
    </div> -->
      <div class="up" v-if="showDisk" @click="showDisk = false">
        <div class="tiao" :class="{ tiaoRorate: audioStatus.playing }">
          <img src="../assets/player/tiao.png" alt="" />
        </div>
        <div class="disk" :class="{ diskrotate: audioStatus.playing }">
          <div
            class="singer"
            :style="`background-image: url('` + picUrl + `');`"
          ></div>
        </div>
      </div>
      <div class="geci" @click="showDisk = true" ref="geci">
        <LyricView :song="song" :audioStatus="audioStatus"></LyricView>
      </div>
      <div class="down">
        <progressBar
          :audioStatus="audioStatus"
          @update-time="$emit('update-time', $event)"
        ></progressBar>

        <div class="btn">
          <div class="wrap">
            <div class="playOrder">
              <span
                class="iconfont"
                v-if="audioStatus.playOrder == 'suiji'"
                @click="$emit('change-playOrder')"
                >&#xe66b;</span
              >
              <!-- 随机 -->
              <span
                class="iconfont"
                v-if="audioStatus.playOrder == 'liebiao'"
                @click="$emit('change-playOrder')"
                >&#xe658;</span
              >
              <!-- 列表 -->
              <span
                class="iconfont"
                v-if="audioStatus.playOrder == 'danqu'"
                @click="$emit('change-playOrder')"
                >&#xe6ae;</span
              >
              <!-- 单曲 -->
            </div>
            <span class="iconfont" @click.stop="$emit('last-song')"
              >&#xe78a;</span
            >
            <div class="playPause">
              <span
                class="iconfont"
                v-if="!audioStatus.playing"
                @click="$emit('bo-fang')"
                >&#xe624;</span
              >
              <span
                class="iconfont"
                v-if="audioStatus.playing"
                @click="$emit('zan-ting')"
                >&#xe629;</span
              >
            </div>
            <span class="iconfont" @click.stop="$emit('next-Song')"
              >&#xe7a5;</span
            >
            <span class="iconfont" @click.stop="$emit('show-list')"
              >&#xea86;</span
            >
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import progressBar from "@/components/progressBar.vue";
import LyricView from "@/components/LyricView.vue";
export default {
  components: {
    progressBar,
    LyricView,
  },
  props: {
    song: Object,
    audioStatus: Object,
    likes: Array,
  },
  data() {
    return {
      showDisk: true,
      geci: [],
      draging: false,
      tmpCurrentTime: this.audioStatus.currentTime,
      loadok: false,
    };
  },
  computed: {
    picUrl: function () {
      if (this.song) {
        return this.song?.picUrl
          ? this.song?.picUrl + `?imageView=1&type=webp&thumbnail=200x0`
          : this.song?.al.picUrl + `?imageView=1&type=webp&thumbnail=200x0`;
      } else {
        return `https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimgs.iavlife.com%2F2017%2F07%2Ff0e14ddffaecd115dfc4ab69ffcc6f72.jpg&refer=http%3A%2F%2Fimgs.iavlife.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1667304347&t=5655a258806b63114987da3f4d655898`;
      }
    },
  },

  created() {
    this.$emit("close-Small");
  },

  watch: {
    // song: function () {
    //   console.log("song变了");
    // },
  },
};
</script>

<style lang="less" scoped>
.BigPlayer {
  width: 375rem;
  height: 100vh;
  background-size: cover;
  background-position: center;
  // filter: blur(2px);
  // background-image: linear-gradient(117deg, #525f6f, #293042 -10%, #525f6f 63%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  position: relative;
  overflow: hidden;

  .bgr {
    position: absolute;
    filter: blur(15rem) brightness(0.6);
    width: 100%;
    height: 100%;
    z-index: -1;
    transform: scale(1.2);
    transition: all 0.5s;
    // background: url('../assets/recommand_bg_2x.png');
  }
  .packup {
    fill: #fff;
    position: absolute;
    left: 19rem;
    top: 17rem;
  }
  .fa-heart {
    transition: all 0.3s;
    color: white;
    position: absolute;
    right: 23rem;
    top: 22rem;
    font-size: 20rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .like {
    color: red;
  }

  .songIfm {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: rgba(255, 255, 255, 0.701);
    width: 100%;
    // max-width: 145rem;
    // overflow: hidden;

    padding-top: 20rem;
    h3 {
      font-size: 20rem;
      padding: 5rem;
      letter-spacing: 3rem;
      white-space: nowrap;
      max-width: 285rem;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    span {
      font-size: 12rem;
      letter-spacing: 1rem;
      display: block;
    }
  }

  .geci {
    width: 375rem;
    height: 370rem;
    margin-bottom: 20rem;
    overflow: hidden;
    display: flex;
    justify-content: center;
    ul {
      li {
        color: white;
        text-align: center;
        font-size: 20rem;
        padding: 10rem 0;
        max-width: 270rem;
      }
    }
  }

  .up {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .tiao {
    width: 75rem;
    height: 28rem;
    margin-bottom: 50rem;
    // background-color: white;
    position: relative;
    transform-origin: center 11rem;
    transform: rotate(-30deg);
    z-index: 5;
    transition: all 0.8s;
    img {
      position: absolute;
      display: block;
      height: auto;
      left: 25rem;
    }
  }
  .tiaoRorate {
    transform: rotate(0deg);
  }
  .disk {
    border-radius: 50%;
    width: 283rem;
    height: 283rem;
    box-sizing: content-box;
    border: 6rem solid rgba(255, 255, 255, 0.037);
    background-clip: padding-box;
    margin-bottom: 100rem;
    background-image: repeating-radial-gradient(
      black 0%,
      black 2%,
      rgb(40, 40, 40) 2%,
      rgb(40, 40, 40) 2.05%
    );
    display: flex;
    justify-content: center;
    align-items: center;
    animation: rotate 8s linear infinite;
    animation-play-state: paused;

    @keyframes rotate {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
    &.diskrotate {
      animation-play-state: running;
    }
    .singer {
      width: 200rem;
      height: 200rem;
      border-radius: 50%;
      // background-color: #fff;
      overflow: hidden;
      // background-image: url("http://p3.music.126.net/eLXUupsFAophg2Sx2CpAjQ==/109951163093327787.jpg");
      background-size: cover;
      // background-position: center;
    }
  }
  .progressBar {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      pointer-events: none;
      padding: 0 15rem;
    }
    .Bar {
      input {
        z-index: 2;
        position: absolute;
        // opacity: 0;
        width: 225rem;
        left: 80rem;
        height: 5rem;
      }
      .virtualBar {
        pointer-events: none;
        width: 240rem;
        position: relative;
        height: 1rem;
        background-color: white;
        border-radius: 1rem;
        // margin-right: 15rem;
        .btn {
          height: 15rem;
          position: absolute;
          opacity: 0;
          display: none;
          top: -7rem;
          width: 15rem;
          border-radius: 50%;
          background-color: #fff;
          left: 0;
        }
      }
    }
  }

  @font-face {
    font-family: "iconfont";
    src: url("@/assets/player/iconfont.ttf") format("truetype");
  }
  .iconfont {
    font-family: "iconfont" !important;
    font-size: 30rem;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .btn {
    width: 100%;
    display: flex;
    justify-content: center;
    .wrap {
      padding: 10rem;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 10rem 30rem;
      span {
        color: white;
        padding: 3rem;
      }
      .playOrder {
        width: 36rem;
        height: 36rem;
        position: relative;
        span {
          display: block;
          position: absolute;
          font-size: 30rem;
        }
      }
      .playPause {
        width: 46rem;
        height: 46rem;
        position: relative;
        span {
          display: block;
          position: absolute;
          font-size: 40rem;
        }
      }
    }
  }
}
</style>
