<template>
  <div class="progressBar">
    <span class="nowTime"
      >{{
        Math.floor(audioStatus.currentTime / 60)
          .toString()
          .padStart(2, "0")
      }}{{
        ":" +
        Math.floor(audioStatus.currentTime % 60)
          .toString()
          .padStart(2, "0")
      }}</span
    >
    <div class="Bar">
      <input
        min="0"
        ref="input"
        type="range"
        :max="audioStatus.duration"
        @input="(draging = true), (drgbtn = $refs.input.value)"
        :value="draging ? $refs.input.value : audioStatus.currentTime"
        @change="changeInput"
      />
      <!--  -->
      <div class="virtualBar">
        <div
          class="btn"
          ref="btn"
          :style="`left:` + (drgbtn2 - 12) + `rem`"
        ></div>
      </div>
    </div>
    <span class="totalTime"
      >{{
        Math.floor(audioStatus.duration / 60)
          .toString()
          .padStart(2, "0")
      }}{{
        ":" +
        Math.floor(audioStatus.duration % 60)
          .toString()
          .padStart(2, "0")
      }}</span
    >
  </div>
</template>

<script>
export default {
  props: {
    audioStatus: Object,
  },
  data() {
    return {
      // tmpCurrentTime: this.audioStatus.currentTime,
      draging: false,
      drgbtn: 0,
    };
  },
  computed: {
    drgbtn2() {
      return (this.drgbtn * 225) / this.audioStatus.duration;
    },
  },
  methods: {
    // log123() {
    //   this.$nextTick(function () {
    //     this.$refs.btn.sytle.left =
    //       (this.drgbtn / this.audioStatus.duration) * 240 - 7.5 + "rem";
    //   });
    // },
    changeInput(event) {
      this.drgbtn = this.$refs.input.value;
      this.$emit("update-time", event.target.value);
      setTimeout(() => {
        this.draging = false;
      }, 10);
    },
  },
  watch: {
    audioStatus: function () {
      // if (!this.draging) {
      //   this.tmpCurrentTime = newValue.currentTime;
      //   console.log("nodraging");
      // } else {
      //   console.log("draging");
      // }
      this.drgbtn = this.$refs.input.value;
    },
  },
};
</script>

<style lang="less" scoped>
.progressBar {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(255, 255, 255);

  display: flex;
  justify-content: center;
  align-items: center;
  span {
    pointer-events: none;
    padding: 0 15rem;
  }
  .Bar {
    input {
      z-index: 2;
      position: absolute;
      opacity: 0;
      width: 240rem;
      // left: 80rem;
      height: 5rem;
    }
    .virtualBar {
      pointer-events: none;
      width: 225rem;
      margin-left: 15rem;
      position: relative;
      height: 1rem;
      background-color: rgba(255, 255, 255, 0.701);
      border-radius: 1rem;
      // margin-right: 15rem;
      .btn {
        height: 15rem;
        position: absolute;
        top: -7rem;
        width: 15rem;
        border-radius: 50%;
        background-color: #fff;
        left: 0;
        // transition: 0.2s;
      }
    }
  }
}
</style>
