<template>
  <li>
    <div class="wrap">
      <div class="left">
        <h3 :class="{ changered: audioStatus.songId == searchResultOne.id }">{{ searchResultOne.name }}</h3>
        <div class="downRow">
          <div class="sq" v-if="searchResultOne.sq"></div>
          <span class="singer" :class="{ changered: audioStatus.songId == searchResultOne.id }"
            >{{ searchResultOne.ar[0].name }}
            {{
              searchResultOne?.ar[1]?.name
                ? "/ " + searchResultOne?.ar[1]?.name
                : ""
            }}
            - {{ searchResultOne.name }}</span
          >
        </div>
      </div>
      <div class="icon">
        <div
          class="play iconfont"
          @click.stop="playsong"
          v-if="
            !audioStatus.playing || searchResultOne.id !== audioStatus.songId
          "
        >
          &#xe624;
        </div>
        <div
          class="pause iconfont"
          @click.stop="$emit('zan-ting')"
          v-if="audioStatus.playing && searchResultOne.id == audioStatus.songId"
        >
          &#xe629;
        </div>
      </div>
    </div>
  </li>
</template>
<script>
export default {
  props: {
    searchResultOne: Object,
    audioStatus: Object,
  },
  data() {
    return {};
  },
  methods: {
    playsong: function () {
      // console.log(this.hotsong);
      this.$emit("play-song", this.searchResultOne);
    },
  },
};
</script>

<style lang="less" scoped>
li {
  padding-left: 10rem;

.changered{
  color: red !important;
}
  .wrap {
    max-width: 100%;
    padding: 6rem;
    padding-left: 0;
    height: 55.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 0.1rem solid rgb(226, 226, 227);
    h3 {
      max-width: 300rem;
      font-size: 17rem;
      padding: 2rem 0;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .downRow {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .sq {
        width: 12rem;
        height: 8rem;
        background-image: url("../assets/sq.png");
        background-size: 162rem auto;
        background-position: 0rem 0rem;
        margin-right: 4rem;
      }
      span {
        display: block;
        padding: 2rem 0;
        font-size: 12rem;
        color: #888;
        text-overflow: ellipsis;
        overflow: hidden;

        max-width: 300rem;
        white-space: nowrap;
      }
    }
    .icon {
      width: 22rem;
      height: 22rem;
      padding: 0 15rem;
      position: relative;
      // background-position: -24rem 0;
      // background-image: url("../assets/sq.png");
      background-size: 166rem 97rem;
      @font-face {
        font-family: "iconfont";
        src: url("@/assets/player/iconfont.ttf") format("truetype");
      }
      .iconfont {
        font-family: "iconfont" !important;
        font-size: 22rem;
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
      }
      .play,
      .pause {
        // background-image: url('@/assets/player');
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}
</style>
