<template>
  <div id="app" :style="song && !showPlayer ? 'padding-bottom:50rem' : ''">
    <transition
      leave-active-class="animate__animated animate__slideOutUp"
      enter-active-class="animate__animated animate__slideInDown"
    >
      <ListPlay
        @delete-one="deleteone($event)"
        @qing-konmg="qingkonmg"
        @play-song="listPlaysong($event)"
        :crtlist="crtlist"
        :nowlist="nowlist"
        :likes="likes"
        :gedan="gedan"
        :showList="showList"
        @update-crtlist="crtlist = $event"
        v-show="audioStatus.listShow"
        @close-list="audioStatus.listShow = false"
        :audioStatus="audioStatus"
      ></ListPlay>
    </transition>
    <router-view
      @change-playOrder="changeplayOrder"
      @update-nowlist="nowlist = $event"
      @play-song="playsong"
      :song="song"
      @show-Small="showPlayer = false"
      @close-Small="closeSmall"
      :audioStatus="audioStatus"
      @zan-ting="zanting"
      @bo-fang="bofang"
      @update-time="$refs.audio.currentTime = $event"
      @i-like="collection($event)"
      @show-list="audioStatus.listShow = true"
      :likes="likes"
      @add-crtPlay="addcrtPlay($event)"
      @update-gedan="updategedan($event)"
      @next-Song="nextSong"
      @last-song="lastsong"
    ></router-view>

    <audio
      @error="Error"
      :src="`https://music.163.com/song/media/outer/url?id=${song?.id}.mp3`"
      ref="audio"
      autoplay
      @pause="correct"
      @play="correct()"
      @ended="nextSong"
      @durationchange="
        audioStatus = { ...audioStatus, duration: $event.target.duration }
      "
      @timeupdate="
        audioStatus = { ...audioStatus, currentTime: $event.target.currentTime }
      "
    ></audio>
    <!-- controls -->
    <PlayerBottom
      v-if="song && !showPlayer"
      :song="song"
      @bo-fang="bofang"
      @show-list="audioStatus.listShow = true"
      @zan-ting="zanting"
      :audioStatus="audioStatus"
      @show-player="showPlayer = true"
    ></PlayerBottom>
    <!-- <BigPlayer v-show='false' :song='song'></BigPlayer> -->
  </div>
</template>
<script>
import PlayerBottom from "@/views/PlayerBottom.vue";
// import BigPlayer from "@/components/BigPlayer.vue";
import ListPlay from "@/components/ListPlay.vue";

export default {
  components: {
    PlayerBottom,
    // BigPlayer,
    ListPlay,
  },
  data() {
    return {
      song: null,
      showPlayer: false,
      audioStatus: {
        playing: false,
        duration: 0,
        currentTime: 0,
        showPlayPage: true,
        songId: 0,
        listShow: false,
        playOrder: "liebiao",
        dijishou: 0,
      },
      crtlist: "当前播放",
      gedan: JSON.parse(localStorage.getItem("gedan")) || [],
      likes: JSON.parse(localStorage.getItem("likes")) || [],
      nowlist: JSON.parse(localStorage.getItem("nowlist")) || [],
    };
  },
  computed: {
    showList() {
      return this.crtlist == "当前播放"
        ? this.nowlist
        : this.crtlist == "我喜欢"
        ? this.likes
        : this.gedan;
    },
    listShow2() {
      return this.audioStatus.listShow;
    },
  },
  created() {
    if (!this.song) {
      this.song = JSON.parse(localStorage.getItem("lastSong"));
    }
  },
  mounted() {
    // console.log(this.$refs.audio.paused);
    this.$refs.audio.pause();
  },
  methods: {
    lastsong() {
      if (this.audioStatus.playOrder == "liebiao") {
        let x = this.audioStatus.dijishou - 1;
        x == 0
          ? (x = this.audioStatus.dijishou - 1 + this.nowlist.length)
          : (x = this.audioStatus.dijishou - 1);
        console.log(x);
        this.playsong(this.nowlist[x]);
      }
      if (this.audioStatus.playOrder == "danqu") {
        this.playsong(this.nowlist[this.audioStatus.dijishou]);
      }
      if (this.audioStatus.playOrder == "suiji") {
        this.playsong(
          this.nowlist[Math.round(Math.random() * this.nowlist.length)]
        );
      }
    },
    nextSong() {
      let x = (this.audioStatus.dijishou + 1) % this.nowlist.length;
      if (this.audioStatus.playOrder == "liebiao") {
        this.playsong(this.nowlist[x]);
      }
      if (this.audioStatus.playOrder == "danqu") {
        this.playsong(this.nowlist[this.audioStatus.dijishou]);
      }
      if (this.audioStatus.playOrder == "suiji") {
        this.playsong(
          this.nowlist[Math.round(Math.random() * this.nowlist.length)]
        );
      }
    },

    changeplayOrder() {
      if (this.audioStatus.playOrder == "liebiao") {
        this.audioStatus.playOrder = "danqu";
      } else {
        if (this.audioStatus.playOrder == "danqu") {
          this.audioStatus.playOrder = "suiji";
        } else {
          this.audioStatus.playOrder = "liebiao";
        }
      }
      this.audioStatus = { ...this.audioStatus };
    },
    Error() {
      console.log("播不了");
      this.correct();
    },

    deleteone(n) {
      console.log(n);
      if (this.crtlist == "当前播放") {
        this.nowlist = this.nowlist.filter((o) => o.id != n.id);
      }
      if (this.crtlist == "我喜欢") {
        this.likes = this.likes.filter((o) => o.id != n.id);
      }
      if (this.crtlist == "歌单") {
        this.gedan = this.gedan.filter((o) => o.id != n.id);
      }
    },
    qingkonmg() {
      if (this.crtlist == "当前播放") {
        this.nowlist = [];
      }
      if (this.crtlist == "歌单") {
        this.gedan = [];
      }
    },
    listPlaysong(n) {
      // console.log(n);
      this.playsong(n);
      if (this.crtlist == "我喜欢") {
        this.nowlist = this.likes;
      }
      if (this.crtlist == "歌单") {
        this.nowlist = this.gedan;
      }
    },
    updategedan(n) {
      this.gedan = n;
    },
    addcrtPlay(n) {
      if (!this.nowlist.some((o) => o.id == n.id)) {
        this.nowlist = [...this.nowlist, n];
      }
    },
    collection(n) {
      console.log(this.likes.some((o) => o.id == n.id));

      this.likes.some((o) => o.id == n.id)
        ? (this.likes = this.likes.filter((o) => o.id != n.id))
        : this.likes.push(n);
    },
    playsong($event) {
      this.song = $event;
      if (this.$refs.audio) {
        try {
          this.$refs.audio.play();
        } catch (error) {
          console.log(error);
        }
      }
      this.$refs.audio.canplay = function () {
        this.bofang();
      };
      this.correct();
    },
    bofang() {
      this.$refs.audio.play();
      this.correct();
    },
    zanting() {
      this.$refs.audio.pause();
      this.correct();
    },
    correct() {
      this.audioStatus = {
        ...this.audioStatus,
        playing: !this.$refs.audio.paused,
        dijishou: this.nowlist.findIndex((o) => o.id == this.song.id),
      };
      console.log(this.nowlist.findIndex((o) => o.id == this.song.id));
    },
    closeSmall() {
      this.showPlayer = true;
    },
  },
  watch: {
    listShow2: function () {
      if (this.listShow2) {
        document.getElementsByTagName("body")[0].style.overflow = `hidden`;
      } else {
        document.getElementsByTagName("body")[0].style.overflow = `visible`;
      }
    },
    song: function () {
      localStorage.setItem("lastSong", JSON.stringify(this.song));
      if (this.song) {
        this.audioStatus.songId = this.song.id;
      }
    },
    likes: function () {
      localStorage.setItem("likes", JSON.stringify(this.likes));
    },
    nowlist: function () {
      localStorage.setItem("nowlist", JSON.stringify(this.nowlist));
    },

    gedan: function () {
      localStorage.setItem("gedan", JSON.stringify(this.gedan));
    },
  },
};
</script>

<style lang="less" scoped>
#app {
  flex-shrink: 0;
}
</style>
