<template>
  <div
    class="playerBottomBox"
    @click="
      $router.push({ path: 'player' });
      $emit('show-player');
    "
  >
    <div class="left">
      <div
        class="headPortrait"
        :style="`background-image: url('` + picUrl + `');`"
        :class="{ rotate1: audioStatus.playing }"
      ></div>

      <p v-if="song?.song?.artists[0]">
        {{ song?.name }} - {{ song?.song?.artists[0]?.name
        }}{{
          song?.song?.artists[1]?.name ? "/" + song?.song?.artists[1]?.name : ""
        }}
      </p>
      <p v-if="song?.ar">
        {{ song?.ar[0]?.name }}
        {{ song?.ar[1]?.name ? "/ " + song?.ar[1]?.name : "" }}
        - {{ song?.name }}
      </p>
    </div>
    <div class="btn">
      <div class="playbtn">
        <span
          class="iconfont"
          v-if="!audioStatus.playing"
          @click.stop="$emit('bo-fang')"
          >&#xe624;
        </span>
        <span
          class="iconfont"
          v-if="audioStatus.playing"
          @click.stop="$emit(`zan-ting`)"
          >&#xe629;
        </span>
      </div>
      <span class="iconfont k-list" @click.stop="$emit('show-list')"
        >&#xea86;
      </span>
    </div>
    <!-- <input type="range" /> -->
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    audioStatus: Object,
  },
  data() {
    return {};
  },
  computed: {
    picUrl: function () {
      if (this.song) {
        return this.song?.picUrl
          ? this.song?.picUrl + `?imageView=1&type=webp&thumbnail=30x0`
          : this.song?.al.picUrl + `?imageView=1&type=webp&thumbnail=30x0`;
      } else {
        return `https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimgs.iavlife.com%2F2017%2F07%2Ff0e14ddffaecd115dfc4ab69ffcc6f72.jpg&refer=http%3A%2F%2Fimgs.iavlife.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1667304347&t=5655a258806b63114987da3f4d655898`;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.playerBottomBox {
  // width: 100%;
  width: 375rem;
  height: 50rem;
  padding: 0 15rem 0 15rem;
  background-color: rgb(251, 253, 255);
  // box-shadow: 0 0 2rem black;
  border-top: 1rem solid rgba(150, 150, 150, 0.407);
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 15;
  @font-face {
    font-family: "iconfont";
    src: url("@/assets/font_4gw5unrel5d/iconfont.ttf?t=1663935436660")
      format("truetype");
  }
  .iconfont {
    font-family: "iconfont" !important;
    font-size: 18rem;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: rgb(64, 70, 90);
  }
  .playbtn {
    position: relative;
    width: 18rem;
    height: 18rem;
    span {
      display: block;
      position: absolute;
    }
  }

  .left {
    display: flex;
    justify-content: center;
    align-items: center;
    @keyframes spin1 {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
    .headPortrait {
      // box-sizing: content-box;
      height: 45rem;
      width: 45rem;
      border-radius: 50%;
      background-size: cover;
      margin-right: 10rem;
      overflow: hidden;
      transform: rotate(0deg);
      // border: 5rem soild black;
      border: 10rem solid rgb(57, 57, 57);
      background-clip: padding-box;
      animation: spin1 5s linear infinite;
      animation-play-state: paused;
    }
    .rotate1 {
      animation-play-state: running;
    }
    p {
      color: rgb(233, 229, 229);
      font-size: 13rem;
      color: black;
      padding-bottom: 5rem;
      max-width: 200rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
  .btn {
    width: 50rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .k-list {
      display: block;
    }
  }
}
</style>
