<template>
  <div class="Lyric">
    <ul class="LyricWrap" :style="`transform: translateY(` + upSlip + `rem);`">
      <li
        v-for="(item, index) in geci"
        :class="{ active: item.time == nowSentence?.time }"
        :key="index"
        ref="sentence"
      >
        {{ item.text }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    audioStatus: Object,
    // geci: Array,
  },
  data() {
    return {
      geci: [],
      geciHeight: [],
      upSlip: 150,
      nowSentenceOrder: -1,
      show: false,
    };
  },

  mounted() {
    let _this = this;
    if (this.song) {
      this.axios
        .get(`https://apis.netstart.cn/music/lyric?id=${this.song.id}`)
        .then((res) => {
          // console.log(res.data.lrc.lyric);
          var arr = res.data.lrc.lyric.split("\n");
          var parsed = arr
            .filter((o) => o)
            .map((o) => {
              var { groups } = o.match(
                /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
              );
              return {
                time: groups.mm * 60 + groups.ss * 1,
                text: groups.text.trim(),
              };
            });
          _this.geci = parsed;
          this.$nextTick(function () {
            // console.log(this.$refs.sentence);
            this.$refs.sentence.forEach((e, i) => {
              if (i) {
                this.geciHeight = [
                  ...this.geciHeight,
                  this.geciHeight[i - 1] + e.offsetHeight,
                ];
                // console.log(i, e.offsetHeight);
              } else {
                this.geciHeight.push(e.offsetHeight);
              }
            });
          });
        });
    }
  },
  computed: {
    nowSentence() {
      if (this.song && this.geci[this.nowSentenceOrder]) {
        return this.geci[this.nowSentenceOrder];
      } else {
        return null;
      }
    },
  },
  methods: {},
  watch: {
    audioStatus: function () {
      let index =
        this.geci.findIndex((o) => o.time > this.audioStatus.currentTime) - 1;
      if (index >= 0) {
        this.nowSentenceOrder = index;
        this.upSlip = this.geciHeight[index] * -1 + 200;
      }
      // this.getLyric()
      if (!this.geciHeight[0]) {
        console.log(this.geciHeight[0]);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.Lyric {
  ul {
    transition: all 0.5s;
    li {
      color: white;
      text-align: center;
      font-size: 15rem;
      padding: 10rem 0;
      max-width: 270rem;
      // transition-property: font-size;
      // transition: all 0.5s;
    }
    .active {
      color: red;
      font-size: 25rem;
      transform: scale(1.1);
    }
  }
}
</style>
