<template>
  <section class="Hotsonglist">
    <div class="loading" v-if="loadingShow" ref="loading">
      <img src="../img/loading.gif" />
    </div>
    <div class="hotop">
      <div class="hotopct">
        <div class="u-hmsprt hoticon"></div>
        <div class="hottime">更新日期：09月21日</div>
      </div>
    </div>
    <ul class="m-sglst">
      <div class="everySong"></div>

      <HotMusic
        v-for="(item, index) in hotsonglist"
        :key="item.id"
        :audioStatus="audioStatus"
        @play-song="playsong($event)"
        :hotsong="item"
        @zan-ting="$emit('zan-ting')"
        :index="index"
        @click.native="$emit('play-song', item), $emit('add-crtPlay', item)"
      ></HotMusic>
    </ul>
    <div class="hotdn"><span class="hotview">查看完整榜单</span></div>
  </section>
</template>
<script>
import HotMusic from "@/components/HotSong.vue";

export default {
  components: {
    HotMusic,
  },
  props: {
    audioStatus: Object,
  },
  data() {
    return {
      hotsonglist: [],
      loadingShow: true,
    };
  },

  created() {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        this.loadingShow = false;
        this.hotsonglist = res.data.playlist.tracks.slice(0, 20);
        // console.log(this.hotsonglist);
      });
  },
  methods: {
    playsong(n) {
      this.$emit("play-song", n);
      this.$emit("add-crtPlay", n);
    },
  },
};
</script>

