<template>
  <div class="searchPage">
    <div class="loading" v-if="loadingShow" ref="loading">
      <img src="../img/loading.gif" />
    </div>

    <div class="searchbox">
      <div class="inputcover">
        <i class="fa fa-search"></i>
        <input
          type="text"
          placeholder="搜索歌曲、歌手、专辑"
          v-model="value"
          ref="inputBox"
          @keydown.enter="search(value), saveValue(value)"
          @keydown.esc="value = ''"
          @input="debounceHint(value)"
        />
      </div>
      <div
        class="closeCircle"
        v-show="value"
        @click="(value = ''), (playlist = false)"
      ></div>
    </div>
    <ul class="playList" v-if="playlistShow">
      <!-- 1<br /><br /><br /><br /><br />1 -->
      <SearchResult
        v-for="(searchResultOne, index) in searchResult"
        :key="searchResultOne.id ? searchResultOne.id : index"
        :searchResultOne="searchResultOne"
        :audioStatus="audioStatus"
        @zan-ting="$emit('zan-ting')"
        @play-song="playsong($event)"
        @click.native="$emit('play-song', searchResultOne),$emit('add-crtPlay', searchResultOne);
"
      ></SearchResult>
    </ul>
    <div class="seekxxx" v-show="hintShow">搜索"{{ value }}"</div>
    <ul class="keywords" v-show="hintShow">
      <li
        class="keyword"
        @click="hintSearch"
        v-for="Kkeyword in keywords"
        :key="Kkeyword.keyword"
      >
        <i class="glass"></i>
        <span>{{ Kkeyword.keyword }}</span>
      </li>
    </ul>
    <div class="hotlist" v-show="!Boolean(value)">
      <h3 class="title">热门搜索</h3>
      <ul class="list">
        <li
          class="item"
          v-for="(hotsch, index) in hotSearch"
          :class="hotsch.first"
          :data-name="hotsch.first"
          :key="index"
          @click="hotSearchClick"
        >
          {{ hotsch.first }}
        </li>
      </ul>
    </div>
    <ul class="record" v-if="!Boolean(value)">
      <SearchRecord
        @so-suo="value = $event"
        v-for="(history, index) in historys"
        :key="history + index"
        :data-text="history"
        :history="history"
        @shan-chu="deleteHistory"
      ></SearchRecord>
    </ul>
  </div>
</template>

<script>
import SearchRecord from "@/components/SearchRecord.vue";
import SearchResult from "@/components/SearchResult.vue";
var _ = require("lodash");

export default {
  components: {
    SearchRecord,
    SearchResult,
  },
  props: {
    audioStatus: Object,
  },
  data() {
    return {
      value: "",
      hotSearch: [],
      keywords: [],
      historys: localStorage.getItem("historys")
        ? JSON.parse(localStorage.getItem("historys"))
        : [],
      playlist: false,
      searchResult: [],
      loadingShow: true,
    };
  },
  computed: {
    hintShow: function () {
      return Boolean(this.value) && !this.playlist;
    },
    playlistShow: function () {
      return Boolean(this.value) && this.playlist;
    },
  },
  created() {
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.loadingShow = false;
      // console.log(event.currentTarget.lastElementChild.innerHTML);
      this.hotSearch = res.data.result.hots;
    });
    this.debounceHint = _.debounce(this.hint, 800);
  },
  // mounted() {
  //   console.log(this.$refs.loading);
  //   this.$refs.loading.style.display = "none";
  // },
  methods: {
    log123(n) {
      console.log(n);
    },
    playsong(n) {
      this.$emit("add-crtPlay", n);
      this.$emit("play-song", n);
    },
    hotSearchClick(event) {
      this.value = event.currentTarget.getAttribute("data-name");
      this.saveValue(this.value);
      this.playlist = true;
      this.search(this.value);
    },
    hintSearch(event) {
      this.value = event.currentTarget.lastElementChild.innerHTML;
      this.search(event.currentTarget.lastElementChild.innerHTML);
      console.log(123 + "(调试)");
    },
    saveValue(n) {
      if (n) {
        if (this.historys.includes(n)) {
          this.historys = this.historys.filter((o) => o != n);
        }
        this.historys.unshift(n);
      }
    },
    deleteHistory($event) {
      this.historys = this.historys.filter((o) => o != $event);
    },
    hint(n) {
      if (!this.playlistShow) {
        this.playlist = false;
        this.keywords = [];

        this.axios
          .get("https://apis.netstart.cn/music/search/suggest?type=mobile", {
            params: {
              keywords: n,
            },
          })
          .then((res) => {
            this.value
              ? (this.keywords = res.data.result?.allMatch)
              : (this.suggests = []);
          });
      }
    },
    search(n) {
      console.log(n);
      this.playlist = true;
      this.$refs.inputBox.blur();
      this.loadingShow = true;
      this.axios
        .get("https://apis.netstart.cn/music/cloudsearch", {
          params: {
            keywords: n,
            limit: 20,
          },
        })
        .then((res) => {
          this.loadingShow = false;
          console.log(res.data.result.songs);
          // console.log("你好鸭");
          this.searchResult = res.data.result.songs;
        });
    },
  },
  watch: {
    historys: function () {
      console.log("watch到了");
      localStorage.setItem("historys", JSON.stringify(this.historys));
    },
  },
};
</script>

<style lang="less" scoped>
.searchPage {
  margin-top: 124rem;
  background-color: rgb(251, 252, 253);
  .loading {
    z-index: 999;
    background-color: white;
    position: absolute;
    width: 375rem;
    top: 176rem;
    height: 491rem;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      display: block;
    }
  }
  .searchbox {
    height: 60rem;
    width: 100%;
    padding: 15rem 10rem;
    border-bottom: 1px solid rgb(238, 239, 240);
    position: relative;
    .closeCircle {
      position: absolute;
      border-radius: 14rem;
      left: calc(100% - 32rem);
      top: calc(50% - 7rem);
      width: 14rem;
      height: 14rem;
      background-image: url("../img/SearchView/SearchHs/close.svg");
      background-repeat: no-repeat;
      background-size: contain;
    }
    .inputcover {
      background-color: rgb(235, 236, 236);
      border-radius: 30rem;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      i {
        color: rgb(201, 201, 202);
        font-size: 13rem;
        padding: 8.5rem;
        border-top-left-radius: 30rem;
      }
      input {
        border: none;
        height: 30rem;
        width: 100%;
        background-color: transparent;
        outline: none;
        // color: rgb(201, 201, 202);
        &::placeholder {
          color: rgb(201, 201, 202);
        }
      }
    }
  }
  .seekxxx {
    height: 50rem;
    margin-left: 10rem;
    padding-right: 10rem;
    font-size: 15rem;
    line-height: 50rem;
    color: #507daf;
    border-bottom: 1px solid rgb(238, 239, 240);
  }
  .keywords {
    .keyword {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      i.glass {
        display: block;
        width: 15rem;
        height: 15rem;
        // padding-left: 10rem;
        background-size: contain;

        background-repeat: no-repeat;
        margin: 0 10rem;
        background-image: url("../img/SearchView/放大镜.svg");
      }
      span {
        display: inline-block;
        padding-right: 10rem;
        font-size: 15rem;
        line-height: 45rem;
        color: #333;
      }
    }
  }
  .hotlist {
    padding: 15rem 10rem 0;
    h3 {
      font-size: 12rem;
      line-height: 12rem;
      color: #666;
    }
    .list {
      margin: 10rem 0 7rem;
      .item {
        display: inline-block;
        height: 32rem;
        margin-right: 8rem;
        margin-bottom: 8rem;
        padding: 0 14rem;
        font-size: 14rem;
        line-height: 30rem;
        border-radius: 32rem;
        border: 1rem solid #d3d4da;

        a {
          line-height: 32rem;
          color: #333;
          text-decoration: none;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: -1rem;
        }
      }
    }
  }
}
</style>
