<template>
  <li class="SearchRecord" @click="log123">
    <i class="k-clock"></i>
    <div class="hisyr">
      <span class="link f-thide">{{ history }}</span>
      <div class="close">
        <i class="a-times" @click.stop="deleteHistory"></i>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: ["history"],
  methods: {
    deleteHistory() {
      // console.log(this.history);
      this.$emit("shan-chu", this.history);
    },
    log123(event) {
      this.$emit('so-suo',event.currentTarget.getAttribute("data-text"))
      this.$parent.search(event.currentTarget.getAttribute("data-text"));
      console.log(123 + "(调试)");
    },
  },
};
</script>

<style lang="less" scoped>
.SearchRecord {
  display: flex;
  // justify-content: space-between;
  align-items: center;
  .k-clock {
    background-image: url("../img/SearchView/SearchHs/clock.svg");
    display: block;
    width: 15rem;
    height: 15rem;
    margin: 0 15rem;
    background-repeat: no-repeat;
    background-size: contain;
  }
  .hisyr {
    height: 45rem;
    border-bottom: 1rem solid rgb(240, 241, 242);

    display: flex;
    justify-content: space-between;
    align-items: center;
    width: calc(100%);

    span {
      margin-right: 10rem;
      font-size: 14rem;
      display: block;
    }
    .close {
      color: rgb(153, 152, 153);
      width: 32rem;
      height: 45rem;
      line-height: 32rem;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      i {
