<template>
  <li>
    <div class="ranking">
      <span>{{ String(index + 1).padStart(2, "0") }}</span>
    </div>
    <div class="wrap">
      <div class="left">
        <h3 :class="{ changered: audioStatus.songId == hotsong.id }">
          {{ hotsong.name }}
        </h3>
        <div class="downRow">
          <div class="sq" v-if="hotsong.sq"></div>
          <span  class="singer" :class="{ changered: audioStatus.songId == hotsong.id }"
            >{{ this.hotsong.ar[0].name
            }}{{ this.hotsong.ar[1] ? " / " + this.hotsong.ar[1].name : "" }} -
            {{ this.hotsong.al.name }}</span
          >
        </div>
      </div>

      <div class="icon">
        <div
          class="play iconfont"
          @click.stop="playsong"
          v-if="!audioStatus.playing || hotsong.id !== audioStatus.songId"
        >
          &#xe624;
        </div>
        <div
          class="pause iconfont"
          @click.stop="$emit('zan-ting')"
          v-if="audioStatus.playing && hotsong.id == audioStatus.songId"
        >
          &#xe629;
        </div>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    hotsong: Object,
    index: Number,
    audioStatus: Object,
  },
  methods: {
    playsong: function () {
      // console.log(this.hotsong);
      this.$emit("play-song", this.hotsong);
    },
  },
};
</script>

<style lang="less" scoped>
li {
  display: flex;
  // justify-content: center;
  // align-items: center;
  padding-left: 10rem;
.changered{
  color: red !important;
}
  .ranking {
    display: block;
    width: 40rem;
    height: 55rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 17rem;
    color: #999;
    margin-left: -10px;
  }
  &:nth-child(-n + 4) {
    .ranking {
      color: #df3436;
    }
  }
  .wrap {
    width: calc(100% - 40rem);
    padding: 6rem;
    padding-left: 0;
    height: 55.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 0.1rem solid rgb(226, 226, 227);
    h3 {
      font-size: 17rem;
      overflow: hidden;
      padding: 2rem 0;
    }
    .downRow {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .sq {
        width: 12rem;
        height: 8rem;
        background-image: url("../assets/sq.png");
        background-size: 162rem auto;
        background-position: 0rem 0rem;
        margin-right: 4rem;
      }
      span {
        display: block;
        padding: 2rem 0;
        font-size: 12rem;
        color: #888;
      }
    }
    .icon {
      width: 22rem;
      height: 22rem;
      padding: 0 15rem;
      position: relative;
      // background-position: -24rem 0;
      // background-image: url("../assets/sq.png");
      background-size: 166rem 97rem;
      @font-face {
        font-family: "iconfont";
        src: url("@/assets/player/iconfont.ttf") format("truetype");
      }
      .iconfont {
        font-family: "iconfont" !important;
        font-size: 22rem;
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
      }
      .play,
      .pause {
        // background-image: url('@/assets/player');
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}
</style>
