<template>
  <li class="EditCard" @click="$router.push({ path: '/playlist', query: { id: item.id } })" >
    <img :src="this.item.picUrl + '?imageView=1&type=webp&thumbnail=124x0'" />
    <h5>{{ this.item.name }}</h5>
    <span
      ><i class="fa fa-headphones"></i
      >{{ playcount(this.item.playCount) }}</span
    >
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  methods: {
    playcount(n) {
      // console.log(typeof n);
      let x = 0;
      n >  100000000
        ? (x =
            Math.floor(n / 100000000) + "." + Math.floor((n % 100000000) / 10000000) + "亿")
        : (x = n);
      n > 9999 && n < 100000000
        ? (x =
            Math.floor(n / 10000) + "." + Math.floor((n % 10000) / 1000) + "万")
        : (x = n);
      return x;
    },
  },
};
</script>
<style lang="less" scoped>
.EditCard {
  width: 123rem;
  margin-bottom: 16px;
  position: relative;
  h5 {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding: 6rem 2rem 0 6rem;
    min-height: 30rem;
    line-height: 1.2;
    font-size: 13rem;
  }
  span {
    position: absolute;
    right: 5px;
    top: 2px;
    z-index: 3;
    padding-left: 13px;
    color: #fff;
    font-size: 12px;
    background-position: 0;
    background-repeat: no-repeat;
    background-size: 11px 10px;
    text-shadow: 1px 0 0 rgba(0, 0, 0, 0.15);
    i {
      // color: black;
      margin-right: 5rem;
    }
  }
}
</style>
