<template>
  <!-- <div class="cardInfo" v-if="playlist"> -->
  <div class="cardInfo">
    <svg
      @click="$router.back()"
      t="1664433791758"
      class="return"
      viewBox="0 0 1024 1024"
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      p-id="2639"
      width="20"
      height="20"
    >
      <path
        fill="#ffff"
        d="M622.650611 284.901749 447.745069 284.901749 447.745069 142.823869 63.980685 334.705038l383.76336 191.882192L447.744046 384.834762l189.391465 0c149.914358 0 224.855164 62.789045 224.855164 188.368158 0 129.928165-77.435627 194.876386-232.338602 194.876386L187.952184 768.079306l0 99.93199L634.146433 868.011296c211.184817 0 316.777737-95.104031 316.777737-285.311071C950.924169 384.178823 841.510224 284.901749 622.650611 284.901749z"
        p-id="2640"
      ></path>
    </svg>
    <div class="loading" v-if="showLoading">
      <img src="../img/loading.gif" alt="" />
    </div>
    <div
      class="top"
      :style="playlist && `background-image: url(${playlist.coverImgUrl})`"
    >
      <div class="noblur">
        <span class="listType">歌单</span>
        <span class="count">{{ count }}</span>
        <img class="listImg" :src="playlist?.coverImgUrl" alt="" />
      </div>
      <div class="listRight">
        <div class="listTitle">{{ playlist?.name }}</div>
        <div class="creator">
          <img :src="playlist?.creator?.avatarUrl" alt="" class="creatorImg" />
          <span class="creatorName">
            {{ playlist?.creator?.nickname }}
          </span>
        </div>
      </div>
    </div>
    <h3 class="songListTag">歌曲列表</h3>
    <ul class="songList">
      <li
        v-for="(songItem, index) in playlist?.tracks"
        :key="songItem.id"
        class="songItem"
      >
        <div class="left">
          <div class="itemSort" :class="{ changered: audioStatus.songId == songItem.id }">
            {{ (index + 1).toString().padStart(2, "0") }}
          </div>
          <div class="itemInfo">
            <div class="itemName" :class="{ changered: audioStatus.songId == songItem.id }">{{ songItem.name }}</div>
            <div class="itemDetail" :class="{ changered: audioStatus.songId == songItem.id }">
              {{ songItem.ar[0].name }} - {{ songItem.al.name }}
            </div>
          </div>
        </div>
        <div class="right">
          <div class="RedLoading" v-show="songItem.id == audioStatus.songId">
            <RedLoading :audioStatus="audioStatus"></RedLoading>
          </div>
          <div
            class="itemPlay"
            @click="playsong(songItem)"
            v-show="songItem.id !== audioStatus.songId"
          ></div>
        </div>
      </li>
    </ul>
    <h4 class="songListTag">精彩评论</h4>
  </div>
</template>

<script>
import RedLoading from "@/components/RedLoading.vue";
export default {
  components: {
    RedLoading,
  },
  props: {
    audioStatus: Object,
  },
  data() {
    return {
      playlist: null,
      showLoading: true,
    };
  },
  computed: {
    count: function () {
      if (this.playlist) {
        return this.playlist.playCount > 100000000
          ? `${(this.playlist.playCount / 100000000).toFixed(1)}亿`
          : this.playlist.playCount > 10000
          ? `${(this.playlist.playCount / 10000).toFixed(1)}万`
          : this.playlist.playCount;
      } else {
        return null;
      }
    },
  },
  methods: {
    getPlayList: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: { id },
        })
        .then((res) => {
          // this.playlist = res.data.playlist;
          this.playlist = res.data.playlist;
          this.showLoading = false;
          // console.log(res.data.playlist.tracks);
          this.$emit("update-gedan", res.data.playlist.tracks);
        });
    },
    playsong(n) {
      // console.log(n.id, "(调试)");
      this.$emit("play-song", n);
      this.$emit("update-nowlist", this.playlist.tracks);
    },
  },
  created: function () {
    this.getPlayList(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
.cardInfo {
  .changered{
  color: red !important;
}
  .return {
    z-index: 5;
    position: fixed;
    top: 6rem;
    left: 15rem;
  }
  .loading {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 375rem;
    height: 65%;
    background-color: white;
    z-index: 7;
    top: 184rem;

    img {
      display: block;
    }
  }
  .top {
    padding: 30rem 10rem 30rem 15rem;
    display: flex;
    background-size: cover;
    background-position: center;
    position: relative;
    overflow: hidden;
    &::before {
      content: "";
      filter: blur(20rem);
      width: 100%;
      height: 100%;
      transform: scale(1.5);
      position: absolute;
      background: inherit;
      top: 0rem;
      left: 0rem;
      z-index: 0;
    }
    &::after {
      content: "";
      background-color: rgba(0, 0, 0, 0.25);
      width: 100%;
      height: 100%;
      transform: scale(1.5);
      position: absolute;

      top: 0rem;
      left: 0rem;
      z-index: 1;
    }
    .noblur {
      position: relative;
      width: 126rem;
      height: 126rem;
      // flex-grow: 1;
      flex-shrink: 0;
      span.listType {
        position: absolute;
        z-index: 10;
        top: 10rem;
        left: 0;
        padding: 0 8rem;
        height: 17rem;
        color: #fff;
        font-size: 10rem;
        text-align: center;
        line-height: 17rem;
        background-color: rgba(217, 48, 48, 0.8);
        border-top-right-radius: 17rem;
        border-bottom-right-radius: 17rem;
      }
      span.count {
        position: absolute;
        z-index: 10;
        right: 5rem;
        top: 2rem;
        padding-left: 13rem;
        color: #fff;
        font-size: 12rem;
        text-shadow: 1rem 0 0 rgb(0 0 0 / 15%);
        background-size: 11px 10px;
        background-position: 0;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml;base64,");
      }
      img.listImg {
        position: absolute;
        z-index: 9;
      }
    }
    .listRight {
      margin-left: 16rem;
      padding-top: 3rem;
      position: relative;
      z-index: 9;

      .listTitle {
        padding-top: 1rem;
        font-size: 17rem;
        line-height: 1.3;
        color: #fefefe;
        height: 44rem;
        display: -webkit-box; //限定超出两行省略
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      .creator {
        display: flex;
        align-items: center;
        position: relative;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        .creatorImg {
          width: 30rem;
          height: 30rem;
          border-radius: 50%;
          margin-right: 5rem;
        }
        .creatorName {
          display: inline-block;
          color: hsla(0, 0%, 100%, 0.7);
          font-size: 14rem;
        }
      }
    }
  }
  .songListTag {
    font-size: 12rem;
    color: #666666;
    background-color: #efeff0;
    padding: 0 10rem;
    line-height: 23rem;
  }
  .songList {
    .songItem {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .itemSort {
        font-size: 17rem;
        color: #999999;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40rem;
      }
      .left {
        display: flex;
        justify-content: center;
        align-items: center;
        // flex-direction: column;
      }
      .itemInfo {
        display: flex;
        flex-direction: column;
        padding: 6rem 0;
        // width: 80%;

        .itemName {
          max-width: 250rem;
          font-size: 17rem;
          line-height: 25.5rem;
          color: #333333;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .itemDetail {
          font-size: 12rem;
          line-height: 18rem;
          color: #888888;
          max-width: 250rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
      .right {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 30rem;
        height: 30rem;
        margin-right: 5rem;
      }
      .itemPlay {
        position: absolute;
        top: 0;
        left: 0;
        background-image: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28…=);
        background-repeat: no-repeat;
        background-size: 166px 97px;
        display: inline-block;
        width: 22rem;
        height: 22rem;
        background-position: -24rem 0rem;
        // padding: 0 18rem;
      }
      .RedLoading {
        position: absolute;
        top: 0;
        left: 0;

        width: 22rem;
        height: 25rem;
      }
    }
  }
}
</style>
